import type { MaybeRef } from '@vueuse/core'
import { type StyleValue, nextTick, ref, unref, watch } from 'vue'

// Based on the baseColor hex code, calculate the text color
export const useTextClass = (backgroundColor: MaybeRef<string>, cutoff = 160) => {
  // Make backgroundColor reactivem, if it's not already
  const brightness = ref(0)
  const textClass = ref('text-black' as 'text-black' | 'text-white')
  const styleType = ref('backgroundColor' as 'backgroundColor' | 'background')
  const mainColor = ref(unref(backgroundColor))
  const style = ref<StyleValue>()

  watch(() => unref(backgroundColor),
    (bgColor: string) => {
      mainColor.value = bgColor?.match(/rgb\((\d+),\s*(\d+),\s*(\d+)\)/)?.[0] || bgColor
      style.value = { background: bgColor } as StyleValue

      if (!bgColor) {
        textClass.value = 'text-black'
        brightness.value = 0
        return
      }
      const hex = bgColor.replace('#', '')
      const r = parseInt(hex.substring(0, 2), 16)
      const g = parseInt(hex.substring(2, 4), 16)
      const b = parseInt(hex.substring(4, 6), 16)

      brightness.value = (r * 299 + g * 587 + b * 114) / 1000
      if (brightness.value > cutoff)
        return textClass.value = 'text-black'
      textClass.value = 'text-white'
    }, { immediate: true })

  return {
    textClass: unref(textClass),
    brightness: unref(brightness),
    styleType: unref(styleType),
    style: unref(style),
    mainColor: unref(mainColor),
  }
}
