<!-- eslint-disable vue/prop-name-casing -->
<script setup lang="ts">
import { computed, inject } from 'vue'
import type { ClientMessage, Recommendation, ServerMessage } from '@/components/ChatWidget.ce.vue'
import { appearance } from '@/composables/utils'

const props = withDefaults(defineProps<{
  // Content
  message: ClientMessage | ServerMessage
  timestamp: number
  isFirst: boolean

}>(), {
})

const provided = inject(appearance)!

const showTime = computed(() => {
  if (props.isFirst)
    return true

  const current = Math.floor(props.timestamp / 1000 / 60)
  const last = Math.floor(props.timestamp / 1000 / 60)
  if (current === last)
    return false
  return true
})

const time = computed(() => {
  // Use Intl.DateTimeFormat to show messages in the user's locale. Show in days if it was issued on the previous calendar day.
  const date = new Date(props.timestamp)
  const now = new Date()
  const yesterday = new Date(now)
  yesterday.setDate(now.getDate() - 1)
  const language = navigator.language
  if (date.toDateString() === now.toDateString()) {
    return new Intl.DateTimeFormat(language, {
      hour: 'numeric',
      minute: 'numeric',
      hour12: false,
    }).format(date)
  }
  else if (date.toDateString() === yesterday.toDateString()) {
    return 'Yesterday'
  }
  else {
    return new Intl.DateTimeFormat(language, {
      hour12: false,
      day: 'numeric',
      month: 'short',
    }).format(date)
  }
})
</script>

<template>
  <div>
    <!-- Bot bubble -->
    <div v-if="'sender' in props.message" class="chat chat-start !py-1 !gap-x-[12px]">
      <div class="chat-image avatar">
        <div class="w-8 rounded-full">
          <img :src="provided.avatar_asset_url">
        </div>
      </div>
      <div v-if="showTime" class="chat-header">
        <time class="text-xs opacity-50">{{ time }}</time>
      </div>
      <div class="rounded-r-lg rounded-tl-lg px-5 bg-[#DDE2EB] text-black leading-[24px] py-4 mr-5 break-words">
        <p>{{ props.message.user_language_text }}</p>
        <p v-if="props.message.source_links && props.message.source_links.length > 0" class="mt-3 text-xs">
          See <a :href="props.message.source_links[0]" :style="{ color: provided.mainColor }" target="_blank">here</a> for more information.
        </p>
      </div>
    </div>

    <!-- Customer bubble -->
    <div v-if="'text' in props.message" class="chat chat-end !py-1 !gap-x-[12px]">
      <div v-if="showTime">
        <time class="text-xs opacity-50">{{ time }}</time>
      </div>
      <p class="rounded-l-lg rounded-tr-lg px-5 leading-6 py-4 ml-5 !break-words max-w-[316px]" :class="provided.textClass" :style="provided.style">
        {{ props.message.text }}
      </p>
    </div>
  </div>
</template>
