<!-- eslint-disable vue/prop-name-casing -->
<script setup lang="ts">
import { useTextareaAutosize } from '@vueuse/core'
import { inject, nextTick, ref } from 'vue'
import { appearance } from '@/composables/utils'

const props = withDefaults(defineProps<{
  // Content
  isLoading: boolean
  // Appearance
}>(), {})

const emit = defineEmits<{
  (e: 'send', message: string): void
}>()

// const messageInput = ref('asdfsdfsdfdsfsdfsdfdsfsdfasdfasdfasfdsadfsd')
const messageInput = ref('')
const provided = inject(appearance)!
const { textarea } = useTextareaAutosize({ input: messageInput })
function maybeSend() {
  if (!messageInput.value || (messageInput.value.trim() === '') || props.isLoading)
    return
  emit('send', messageInput.value)
  messageInput.value = ''
  nextTick(() => {
    textarea.value!.style.height = '1px'
  })
}
</script>

<template>
  <div class="h-full bg-transparent w-full h-max py-2 pr-4">
    <form class="flex items-center" @submit.prevent="maybeSend()">
      <textarea
        ref="textarea"
        v-model="messageInput"
        class="textarea w-full resize-none focus:outline-none focus:shadow-outline line-3 border-none bg-transparent text-[16px] !leading-[24px] max-h-[144px] align-middle !px-[16px] !py-[8px]"
        type="text"
        :placeholder="provided.message_placeholder"
        @keydown.enter.prevent="maybeSend()"
      />
      <button
        class="btn btn-ghost hover:!border-none focus:!border-none btn-sm btn-square" type="submit" :disabled="isLoading"
      >
        <div class="w-6 h-6">
          <svg xmlns="http://www.w3.org/2000/svg" :fill="provided.mainColor" viewBox="0 0 32 32"><path d="m27.45 15.11l-22-11a1 1 0 0 0-1.08.12a1 1 0 0 0-.33 1L6.69 15H18v2H6.69L4 26.74A1 1 0 0 0 5 28a1 1 0 0 0 .45-.11l22-11a1 1 0 0 0 0-1.78Z" /></svg>
        </div>
      </button>
    </form>
  </div>
</template>
