<!-- eslint-disable vue/prop-name-casing -->
<script setup lang="ts">
import { inject, ref } from 'vue'
import type { Recommendation } from './ChatWidget.ce.vue'
import { appearance } from '@/composables/utils'

const props = withDefaults(defineProps<{
  products: Recommendation[]
  // Styling
}>(), {
})
const emit = defineEmits<{
  (e: 'scrollTo', itemId: string): void
}>()
const provided = inject(appearance)!
function recommend(productUrl: string, itemId: string) {
  const url = new URL(productUrl)
  history.pushState({ monoloqItemId: itemId }, '', url)
  window.location.assign(url)
}

const hideShadow = ref(false)
</script>

<template>
  <div class="w-full h-max carousel carousel-center px-4 pt-0 pb-3 space-x-4 my-auto relative pl-5">
    <div v-for="product in props.products" :key="product.item_id" class="carousel-item h-max w-[284px] cursor-pointer " @click="recommend(product.url, product.item_id)">
      <div class="w-full h-full flex rounded-lg px-3 py-2 text-center duration-200 bg-[#DDE2EB] transition-all" :class="{ recommendation: !hideShadow }">
        <div class="flex flex-col text-left flex-1 mt-3">
          <h2 class="text-lg font-bold">
            {{ `${product.title}` }}
          </h2>
          <p class="text-md text-neutral/70 mt-6 mb-2">
            {{ `$${product.price}` }}
          </p>
          <div class="flex-1" />
          <div class="flex justify-start">
            <button class="btn btn-xs hover:opacity-80 text-[12px] px-[8px] h-[24px] capitalize border-none btn-primary" :style="{ backgroundColor: provided.mainColor }" :class="provided.textClass" @click.stop="emit('scrollTo', product.item_id)" @mouseenter="hideShadow = true" @mouseleave="hideShadow = false">
              Preview
            </button>
          </div>
        </div>
        <figure><img :src="product.images[product.match_image_idx || 0]?.url" alt="shoes" class="h-[144px] rounded-md"></figure>
      </div>
    </div>
  </div>
</template>
