<!-- eslint-disable vue/prop-name-casing -->
<script setup lang="ts">
import { computed, inject } from 'vue'
import { appearance } from '@/composables/utils'

const props = withDefaults(defineProps<{
  isOpen: boolean
}>(), {
  isOpen: true,
})

const emit = defineEmits<{
  (e: 'toggle'): void
}>()

const provided = inject(appearance)!

const cssClasses = computed(() => {
  const classes = []
  provided.value.chat_button_style === 'round' ? classes.push('rounded-full') : classes.push('rounded-md')
  return classes
})
</script>

<template>
  <button
    v-show="!props.isOpen"
    class="flex w-16 h-16 rounded-full cursor-pointer hover:scale-105 duration-200 shadow-xl transition-all z-50"
    :class="[...cssClasses, provided.textClass]"
    :style="provided.style"
    @click="emit('toggle')"
  >
    <svg xmlns="http://www.w3.org/2000/svg" class="w-8 h-8 mx-auto my-auto" viewBox="0 0 24 24"><path d="M20 2H4c-1.103 0-2 .894-2 1.992v12.016C2 17.106 2.897 18 4 18h3v4l6.351-4H20c1.103 0 2-.894 2-1.992V3.992A1.998 1.998 0 0 0 20 2z" fill="currentColor" /></svg>
  </button>
</template>
