import { defineCustomElement } from 'vue'

import { devHost } from '~/composables/utils'
import VueChatWidget from '@/components/ChatWidget.ce.vue'

export const ChatWindow = defineCustomElement(VueChatWidget)
export const installWidget = () => customElements.define('monoloq-widget', ChatWindow)

export async function register(tagName = 'monoloq-widget') {
  console.info('Installing widget...')

  // Install fonts
  const font = document.createElement('link')
  font.href = 'https://fonts.googleapis.com/css2?family=Nunito&display=swap'
  font.rel = 'stylesheet'
  document.head.appendChild(font)

  let props = {}
  try {
    const res = await fetch(
    `${String(import.meta.env.VITE_API_URL)}/settings/appearance?host_domain=${devHost('appearance')}`,
    )
    props = await res.json() as Record<string, any>
  }
  catch (e) {
    console.error('Failed to fetch widget config', e)
  }

  customElements.define(tagName, ChatWindow)
  document.body.appendChild(
    new ChatWindow({ ...props }),
  )
  console.info('Monoloq widget v8 registered.')
}
