<!-- eslint-disable vue/prop-name-casing -->
<script setup lang="ts">
import { TransitionExpand, TransitionSlide } from '@morev/vue-transitions'
import { type ComputedRef, computed, provide, ref, watch, watchEffect, withDefaults } from 'vue'
import { createFetch, useStorage, useThrottleFn } from '@vueuse/core'
import { appearance, devHost, prepareMessage } from '@/composables/utils'

import ChatHeader from '@/components/ChatHeader.vue'
import ChatBubble from '@/components/ChatBubble.vue'
import ChatInput from '@/components/ChatInput.vue'
import WidgetButton from '~/components/WidgetButton.ce.vue'
import MonoloqLogo from '@/components/Monoloq.vue'
import LoaderAnimation from '@/components/Loader.vue'
import ChatProducts from '@/components/ChatProducts.vue'
import MiniPreview from '@/components/MiniPreview.vue'
import { useTextClass } from '@/composables/useTextClass'

export interface BaseMessage {
  conversation_id: string
  // TODO: Unify timestamps and names
  timestamp: number
  state?: string
}
export interface ClientMessage extends BaseMessage {
  text: string
}

export interface Recommendation {
  match_image_idx: number
  item_id: string
  title: string
  price: number
  description: string
  url: string
  primary_category: string
  categories: string[]
  features: Record<string, string | string[]>
  brand: string
  language: string
  tags: string[]
  handle: string
  options: Record<string, string | string[]>
  images?: { alt_text?: string; is_features: boolean; url: string; local_path: string; match_image_idx: number }[]
}

export interface ServerMessage extends BaseMessage {
  bot_language_text: string
  user_language_text: string
  sender: 'bot'
  recommended_products?: Recommendation[]
  source_links?: string[]
}

export interface Apperance {
  base_color?: string
  avatar_asset_url?: string
  header_text?: string
  message_placeholder?: string

  chat_button_style?: 'round' | 'tab'
  chat_button_label?: boolean
  chat_button_text?: string
  chat_button_color?: string
  chat_button_position?: 'bottom_left' | 'bottom_right'

  enable_on_mobile?: boolean
  host_name?: string
  show_button_widget?: boolean
  fixed?: boolean
  _mockLoading?: boolean
  _mockMessage?: ServerMessage | ClientMessage
}

export interface DefaultAppearance {
  base_color: '#4fafe4'
  avatar_asset_url: 'https://cdn.jsdelivr.net/gh/paraloq/chat_widget/umd/stock-avatar.jpeg'
  message_placeholder: 'Type your message here...'
  chat_button_style: 'round'
  chat_button_label: false
  chat_button_position: 'bottom_right'

  enable_on_mobile: false
  host_name: 'ariasilk.com'
  show_button_widget: true
  fixed: true
  _mockLoading: false
}

export type Injection = ComputedRef<ReturnType<typeof withDefaults<Apperance, DefaultAppearance>> & ReturnType<typeof useTextClass>>
type ConversationId = string & { __type: 'ConversationId' }
const props = withDefaults(defineProps<Apperance>(), {
  base_color: '#4fafe4',
  avatar_asset_url: 'https://cdn.jsdelivr.net/gh/paraloq/chat_widget/umd/stock-avatar.jpeg',
  message_placeholder: 'Type your message here...',
  chat_button_style: 'round',
  chat_button_label: false,
  chat_button_position: 'bottom_right',

  enable_on_mobile: false,
  host_name: 'ariasilk.com',
  show_button_widget: true,
  fixed: true,
  _mockLoading: false,
})

const provided = computed(() => ({ ...props, ...useTextClass(props.base_color) }))
provide(appearance, provided)

const positionCss = computed(() => {
  const classes = ['bottom-1 right-1 ']
  props.chat_button_position === 'bottom_right' ? classes.push(' sm:right-5') : classes.push('sm:left-5')
  props.fixed ? classes.push('fixed sm:ml-0 sm:bottom-5') : classes.push('absolute')
  props.enable_on_mobile ? classes.push('') : classes.push('hidden sm:flex')
  return classes
})

// Track dev mode
const IS_DEV = import.meta.env.DEV
// Content variables
const retries = ref(0)
// TODO: Find out why it is updating on reload
const conversationId = useStorage('monoloqConversationId', '' as ConversationId)
const messages = useStorage<(ClientMessage | ServerMessage)[]>('monoloqChatBotMessages', [])
const visited = useStorage<Set<string>>('monoloqVisited', new Set())

// Product recommendation logic
const products = ref<Recommendation[]>()
const scrollToItemId = ref<string>()
const setProducts = (recommendation: Recommendation[], itemId?: string) => {
  products.value = recommendation
  scrollToItemId.value = itemId
}
const closeProducts = () => {
  products.value = undefined
  scrollToItemId.value = undefined
}

// Display variables
const refChat = ref<HTMLDivElement>()
const showWidget = ref(true)
const isLoading = ref(false)
const isChatOpen = useStorage('monoloqIsChatOpen', false)

// TODO: Replace all devHost() with computed in final version
const currentHost = computed(() => window.location.hostname)

const fetch = createFetch({
  baseUrl: import.meta.env.VITE_API_URL,
  fetchOptions: {
    method: 'GET',
  },
  options: {
    refetch: false,
    async beforeFetch({ options, cancel }) {
      const additionalHeaders = { 'x-api-key': import.meta.env.VITE_API_KEY, 'Content-Type': 'application/json' }
      if (retries.value > 30 || !showWidget.value)
        cancel()
      const newOptions = { ...options, headers: { ...options.headers, ...additionalHeaders } }
      return { options: newOptions }
    },
    afterFetch(ctx) {
      ctx.data.timestamp = Date.parse(ctx.data.timestamp)
      return ctx
    },
    async onFetchError(ctx) {
      if (ctx.response?.status === 429) {
        showWidget.value = false
        throw new Error('Monoloq limit reached. Too many requests.')
      }

      const errorMessage = {
        bot_language_text: ctx.data,
        conversation_id: conversationId.value,
        sender: 'bot',
        timestamp: (new Date()).getTime(),
        user_language_text: ctx.data || ctx.error?.message || 'Unknown error',
        recommended_products: undefined,
      }
      ctx.data = errorMessage
      return ctx
    },
  },
})

async function onSend(inputMessage: string) {
  // If ChatGPT is still loading, don't send message
  isLoading.value = true
  if (!refChat.value || !conversationId.value)
    return
  const date = new Date()
  const message: ClientMessage = {
    text: prepareMessage(inputMessage),
    timestamp: date.getTime(),
    conversation_id: conversationId.value,

  }
  // Add message to chat log
  messages.value.push(message)
  // Send message to server
  const serverResponse = await handleMessage(message)

  const maybeRecommendedProduct = serverResponse?.recommended_products
  if (!maybeRecommendedProduct?.length)
    return
  isLoading.value = true
  await new Promise(resolve => setTimeout(resolve, 1000))
  isLoading.value = false
  setProducts(maybeRecommendedProduct)
}

const comesFromDomain = computed(() => {
  if (!document.referrer)
    return false
  const refferer = new URL(document.referrer)
  // TODO: Remove this
  if (window.location.hostname === 'localhost' && IS_DEV)
    return true
  return refferer.hostname === window.location.hostname
})

function scrollToBottom() {
  if (!refChat.value)
    return
  const start = refChat.value!.scrollTop
  const end = refChat.value!.scrollHeight
  const distance = end - start
  let elapsed = 0
  const duration = 100
  function animate() {
    elapsed += 16
    const fraction = Math.min(1, elapsed / duration)
    refChat.value!.scrollTop = start + distance * fraction
    if (fraction < 1)
      requestAnimationFrame(animate)
  }
  requestAnimationFrame(animate)
}

// Add function that gets a callback function receiving an object as a parameter
async function requestAndPush(
  url: string,
  params?: Record<string, string | number>,
  fetchOptions: RequestInit = {}) {
  isLoading.value = true
  const baseUrl = new URL(url, import.meta.env.VITE_API_URL)

  for (const prop in params)
    baseUrl.searchParams.set(prop, String(params[prop]))
  baseUrl.searchParams.set('host_domain', devHost('initiate'))

  let retries = 0
  const { data: msg, execute, statusCode } = await fetch(baseUrl.toString(), fetchOptions).json<ServerMessage>()
  while (statusCode.value && statusCode.value >= 500 && retries < 2) {
    await execute()
    retries += 1
  }

  isLoading.value = false
  messages.value.push(msg.value!)
  return msg.value
}

async function handleMessage(body: ClientMessage) {
  return await requestAndPush('/chat/handle_message',
    undefined,
    { method: 'POST', body: JSON.stringify(body) },
  )
}

async function initiate() {
  const msg = await requestAndPush('/chat/initiate',
    { language: IS_DEV ? 'en' : document.documentElement.lang.slice(0, 2) || 'en' },
  )
  conversationId.value = msg?.conversation_id as ConversationId
}

async function resume() {
  await requestAndPush('/chat/resume', { conversation_id: conversationId.value })
}

async function pitch(product_id: string) {
  await requestAndPush('/chat/pitch_product', { conversation_id: conversationId.value, product_id })
}

watchEffect(() => {
  if (!messages.value.length || !refChat.value)
    return
  scrollToBottom()
})

watchEffect(() => {
  if (props.enable_on_mobile)
    return
  if (window.matchMedia('only screen and (max-width: 480px)').matches)
    showWidget.value = false
})

watch(() => conversationId.value,
  () => {
    if (!conversationId.value)
      return
    visited.value.clear()
  })

watch(() => props._mockMessage,
  (mockMessage) => {
    if (!mockMessage)
      return
    messages.value.push(mockMessage)
  }, { immediate: true })
async function connect() {
  if (window.location.hostname === 'localhost' && messages.value.length !== 0)
    return
  if (messages.value.length === 0)
    return await initiate()
  if (!conversationId.value)
    return await initiate()
  if (!comesFromDomain.value) {
    const isStale = messages.value[messages.value.length - 1].timestamp < (new Date()).getTime() - 1000 * 60 * 60 * 8
    if (isStale)
      return await resume()
  }
  const { monoloqItemId } = window.history.state
  if (monoloqItemId) {
    if (visited.value.has(monoloqItemId))
      return
    await pitch(monoloqItemId)
    visited.value.add(monoloqItemId)
  }
}

connect().then()

const throttledConnect = useThrottleFn(() => {
  messages.value = []
  connect()
}, 3000)

const onClear = async () => {
  await throttledConnect()
}

const onClose = () => {
  isChatOpen.value = false
}

const onToggle = () => {
  isChatOpen.value = true
}
</script>

<template>
  <TransitionSlide :offset="[0, 200]">
    <div
      v-if="isChatOpen && showWidget"
      class="!font-sans !text-base text-black flex flex-col m-2 sm:m-0 bg-white rounded-2xl my-auto sm:mr-2 h-full w-full max-h-[694px] max-w-[372px] sm:h-[694px] sm:w-[372px] drop-shadow-2xl z-40 select-none"
      :class="[...positionCss]"
    >
      <!-- Chat Header -->
      <ChatHeader class="!h-[64px] flex-none" @clear="onClear()" @close="onClose()" />
      <div class="relative flex flex-col h-[630px] max-h-[630px] flex-1">
        <TransitionExpand :duration="500">
          <ChatProducts v-if="products" :products="products" :scroll-to="scrollToItemId" @close-products="closeProducts()" />
          <div v-else class="absolute w-full" />
        </TransitionExpand>
        <div ref="refChat" class="overflow-y-auto scroll-behavior-smooth px-3 py-3 bg-transparent relative flex-1">
          <TransitionSlide group :offset="[0, 16]">
            <!-- TODO: Transform date in a parsing step of the datamodel to reduce computation time -->
            <template v-for="message, index in messages" :key="message.timestamp">
              <ChatBubble :timestamp="message.timestamp" :message="message" :is-first="index === 0" @open-products="setProducts" />

              <div v-if="'sender' in message && message.recommended_products" class="relative flex flex-col pt-3">
                <MiniPreview :products="message.recommended_products" @scroll-to="(itemId) => setProducts(message.recommended_products!, itemId)" />
              </div>
            </template>
          </TransitionSlide>
          <LoaderAnimation v-show="isLoading || props._mockLoading" class="scale-50 ml-9" />
        </div>

        <div
          class="text-center bg-transparent text-xs py-2 flex flex-col justify-center items-center border-b-2 flex-none"
        >
          <a href="https://www.monoloq.ai/" target="_blank" class="flex items-center hover:text-neutral text-neutral">
            <div class="mr-2">
              Powered by
            </div>
            <MonoloqLogo class="h-4 my-auto" />
          </a>
        </div>

        <!-- Chat Input -->
        <ChatInput class="flex-none" :is-loading="isLoading" @send="onSend" />
      </div>
    </div>
    <div v-else class="bottom-0 right-0 w-[372px]" />
  </TransitionSlide>
  <slot name="widgetButton" :toggle="onToggle">
    <WidgetButton
      v-if="props.show_button_widget && showWidget"
      :class="[...positionCss]"
      :chat_button_position="props.chat_button_position"
      :is-open="isChatOpen"
      @toggle="onToggle()"
    />
  </slot>
</template>

<style>
@import url("@/assets/input.css");
</style>
