<script setup lang="ts">
import { onMounted, ref } from 'vue'
import type { Recommendation } from './ChatWidget.ce.vue'

const props = withDefaults(defineProps<{
  products: Recommendation[]
  scrollTo?: string
}>(), {
})

const emit = defineEmits<{
  (e: 'closeProducts'): void
  (e: 'close'): void
}>()

const chatProducts = ref<HTMLDivElement>()
function recommend(productUrl: string, itemId: string) {
  const url = new URL(productUrl)
  history.pushState({ monoloqItemId: itemId }, '', url)
  window.location.assign(url)
}

onMounted(async () => {
  if (!props.scrollTo)
    return
  const el = chatProducts.value?.querySelector(`#monoloq-${props.scrollTo}`)
  if (!el)
    return
  await new Promise(resolve => setTimeout(resolve, 310))
  el.scrollIntoView({ behavior: 'smooth', block: 'center' })
})
</script>

<template>
  <!-- toolbar -->
  <!-- Backgrounds
    1. Carousel with no background and the cards with backdrop-blur-sm and bg-white/50 or something dark
    2. Carousel with dark background bg-black/30 no blur but the card with backdrop-blur-sm bg-white/50
    3. -->
  <div ref="chatProducts" class="absolute bottom-0 right-0 h-[100%] overflow-auto w-full z-50 bg-black/30 backdrop-blur-sm flex">
    <button class="btn btn-circle btn-ghost hover:!border-none focus:!border-none btn-sm absolute top-3 right-3 z-50 bg-black/60 text-back" @click="emit('closeProducts')">
      <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" /></svg>
    </button>
    <div class="w-full h-max carousel carousel-center p-4 space-x-4 my-auto">
      <div v-for="product in props.products" :id="`monoloq-${product.item_id}`" :key="product.item_id" class="carousel-item h-max w-11/12 cursor-pointer" @click="recommend(product.url, product.item_id)">
        <div class="card w-full h-full stuff backdrop-blur-sm card-compact !bg-black/60 group">
          <figure>
            <img :src="product.images?.[product.match_image_idx || 0]?.url" alt="shoes">
          </figure>

          <div class="card-body text-white text-center">
            <p class="text-xl mt-4 font-bold text-opacity-60 group-hover:text-opacity-100 transition-all duration-200">
              {{ `${product.title}` }}
            </p>
            <div>
              <p class="text-base text-white/40 line-clamp-6">
                {{ product.description }}
              </p>
            </div>
            <p class="text-lg mt-2">
              {{ `$${product.price}` }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
